.header-color {
  background: #833ab4;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #536976, #292e49);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #536976, #292e49);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #536976;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #536976, #292e49);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #536976, #292e49);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.projects-grid {
  display: flex;
}

/* Resume Page css */

.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

/* contact page css */

.contact-body {
  margin: auto;
  position: relative;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: "Anton", sans-serif;
}

.contact-grid p {
  font-family: "Oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 3rem;
}

/* contact page css */

.avatar-img {
  height: 250px;
  width: 250px;
  border-radius: 10px;
  margin-top: 2em;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
  width: 80%;
  margin: auto;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: 0 auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

@media (max-width: 767.98px) {
  .small-font {
    font-size: 24px;
  }
  .banner-text h1 {
    font-size: 50px;
  }
  .social-links {
    width: 90%;
  }
}
@media (max-width: 575.98px) {
  .small-font {
    font-size: 18px;
  }
  .social-links {
    width: 90%;
  }
  .banner-text h1 {
    font-size: 40px;
  }
  .social-links i {
    font-size: 3em;
    padding: 5px;
    padding-bottom: 10px;
  }
}
